.container {
    align-content: center;
    display: flex;
    flex-direction: column;
    font-family: 'Titillium Web', sans-serif;
    font-weight: bold;
    justify-content: space-around;
    margin: 5px;
    width: 100%;
    background-color: #1b1b1b;
    box-shadow: 0.5px 0.5px 8px #ffeaec

}

.top {
    display: flex;
    flex-wrap: wrap-reverse;
    width: auto;
    flex-direction: inherit;
}

.left, .right {
    flex-wrap: wrap;
    text-align: left;

}

.left {
    flex-basis: 70%;
    margin: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;


}

.right {
    flex: 1 1;
    display: flex;
    flex-direction: column;

}

.right-sub {

    width: auto;
    margin: 4px 10px 4px 4px;
}

.middle {
    display: flex;
    flex-wrap: wrap;
    height: auto;
    justify-content: center
}

.prestige{
margin: auto;
}

.logo {
    height: 100px;
}




.card{
    margin: 10px;
    /*animation: shadowBox 3s infinite;*/
    height: auto;
    text-align: center;
    color: #a6a6a6;
    background-color: #1f1f1f;
    /*width: -webkit-fill-available;*/
}

.card-sub {
    background-color: #2a2a2a;
    margin: 10px;
    text-wrap: normal;
}

.card-top {
    background-color: #2a2a2a;
}

.card-context {
    margin: 5px;
    color: white;
    font-size: 60px;
    height: auto;
    line-height: 111px;
    background-color: #1f1f1f;
}

@-webkit-keyframes shadowBox {
    0% {box-shadow: 2px 1px 18px #ff9400;}
    50% {box-shadow: 2px 1px 5px #2b1503;}
    100% {box-shadow: 2px 1px 18px #ff9400;}

}

@keyframes shadowBox {
    0% {box-shadow: 2px 1px 18px #ff9400;}
    50% {box-shadow: 2px 1px 5px #2b1503;}
    100% {box-shadow: 2px 1px 18px #ff9400;}

}
.App {
    background-color: black;
    color: orange;
    display: flex;
}

.player-list{
    display: inline;
    margin: 10px;
    font-family: 'Titillium Web', sans-serif;
    font-weight: bold;
}

.player-list:hover {
    color: red;
}



